<template>
  <div class="col-3">
    <router-link :to="{ name: 'users-profile', params: { id: user.id }}">
      <img :src="user.image" width="140px" height="140px">
    </router-link>
    <h2>{{user.name}}</h2>
    <span class="badge badge-secondary">追蹤人數: {{user.FollowerCount}}</span>
    <p class="mt-3">
      <button v-if="user.isFollowed" @click.stop.prevent="deleteFollowing(user.id)" type="button" class="btn btn-danger">
        取消追蹤
      </button>
      <button v-else @click.stop.prevent="addFollowing(user.id)" type="button" class="btn btn-primary">
        追蹤
      </button>
    </p>
  </div>
</template>

<script>
import usersAPI from './../apis/users'
import { Toast } from './../utils/helpers'

export default {
  props: {
    initialUser: {
      type: Object,
      required: true
    }
  },
  data() {
    return {
      user: this.initialUser
    }
  },
  methods: {
    async addFollowing(userId) {
      try {
        const { data } = await usersAPI.addFollowing({ userId })

        if (data.status === 'error') {
          throw new Error(data.message)
        }

        this.user = {
          ...this.user,
          isFollowed: true
        }
      } catch (error) {
        Toast.fire({
          icon: 'error',
          title: '無法加入追蹤，請稍後再試'
        })
      }
    },
    async deleteFollowing(userId) {
      try {
        const { data } = await usersAPI.deleteFollowing({ userId })

        if (data.status === 'error') {
          throw new Error(data.message)
        }

        this.user = {
          ...this.user,
          isFollowed: false
        }
      } catch (error) {
        Toast.fire({
          icon: 'error',
          title: '無法取消追蹤，請稍後再試'
        })
      }
    }
  }
}
</script>